import { Controller } from '@ali/4ever-cangjie';
import { CLEAR_PARAGRAPH_STYLE, CLEAR_STYLES } from '../actions';
import clearParagraphStyle from '../commands/clearParagraphStyle';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === CLEAR_STYLES) {
    return controller.command('clearStyle', payload.ranges);
  } else if (type === CLEAR_PARAGRAPH_STYLE) {
    return controller.command(clearParagraphStyle, payload.range);
  }
  return next();
}
